<template>
    <div>
        <br>
        <el-button type="primary"  round  @click="dialogVisible = true">获取wife</el-button>
            <el-table :data="wifeList" :cell-style="rowClass" border style="width: 521px; left: 10%; margin-top: 50px;">
              <el-table-column  prop="id" label="ID" width="120"> </el-table-column>
              <el-table-column  prop="name"  label="老婆"  width="150"></el-table-column>
              <el-table-column prop="fate_id" label="命座" width="130"></el-table-column>
              <el-table-column label="操作" width="120px">
                <template slot-scope="scope">
                  <el-button @click="del(scope.row.id)" type="text" size="small">删除</el-button>
                </template>
</el-table-column>
</el-table>

<el-dialog title="wife信息" :visible.sync="dialogVisible" width="30%">
    <el-form>
        <el-form-item label="ID">
            <el-input v-model="form.id" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="老婆">
            <el-input v-model="form.name" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="命座">
            <el-input v-model="form.fate" placeholder=""></el-input>
        </el-form-item>
    </el-form>
    <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="onSubmit">确 定</el-button>
  </span>
</el-dialog>

</div>
</template>

<script>
    import request from "../utils/request"
    export default {
        methods: {
            onSubmit() {
                request
                    .post(`/wifes`, this.form)
                    .then(() => {
                        this.getWifeList();
                        this.dialogVisible = false;
                    });
            },
            del(id) {
                this.$confirm("此操作将永久删除该文件, 是否继续?", "提示", {
                        confirmButtonText: "确定",
                        cancelButtonText: "取消",
                        type: "warning"
                    })
                    .then(() => {
                        request
                            .delete(`/wifes/${id}`)
                            .then(() => {
                                this.$message({
                                    type: "success",
                                    message: "删除成功!"
                                });
                                this.getWifeList();
                            });
                    })
                    .catch(() => {
                        this.$message({
                            type: "info",
                            message: "已取消删除"
                        });
                    });
            },

            rowClass() { //表头居中显示
                return "text-align:center"
            },
            getWifeList() {
                request
                    .get(`/wifes`)
                    .then(res => {
                        console.log(res.data.data);
                        this.wifeList = res.data.data;
                    });
            },
            getFateList() {
                request.get("/fate").then((res) => {
                    this.fateList = res.data;
                })
            }
        },
        data() {
            return {
                wifeList: [],
                fateList: [],
                dialogVisible: false,
                form: {
                    name: '',
                    fate: '',
                    id: null,
                }
            }
        },
        created() {
            this.getWifeList();
            this.getFateList();
        },
    };
</script>

<style>
    .el-table,
    .el-table tr,
    .el-table td,
    .el-table th {
        /* 表格字体颜色 */
        color: pink;
        /* 表格边框颜色 */
        border: 0.5px solid pink;
        text-align: center;
    }
    
    .el-table,
    .el-table__expanded-cell {
        background-color: transparent;
    }
    /* 表格内背景颜色 */
    
    .el-table,
    .el-table tr,
    .el-table td,
    .el-table th {
        background-color: transparent!important;
    }
</style>