<template>
  <div class="home">
      <br>
    <el-button plain round="" @click="getMsg">获取数据</el-button>
     <h2>数据: {{msg}}</h2>
  </div>
</template>

<script>
    import request from "../utils/request"
    export default {
        data() {
            return {
                msg: "",
            }
        },
        methods: {
            getMsg() {
                request.get(`/hello`).then((res) => {
                    this.msg = res.data.data[0].name;
                    //res.data是一个对象,有个data属性
                })
            },
        },
        created() {}
    }
</script>