<template>
    <div>
        <br>
        <el-button type="primary"  round @click="dialogVisible = true">获取命座</el-button>
        <el-table :data="fateList" :cell-style="rowClass" border style="width: 400px; left: 2%; margin-top: 50px;">
          <el-table-column  fixed prop="id" label="ID" width="120"> </el-table-column>
          <el-table-column  prop="name"  label="命座"  width="158"></el-table-column>
          <el-table-column label="操作" width="120px">
            <template slot-scope="scope">
              <el-button @click="del(scope.row.id)" type="text" size="small">删除</el-button>
            </template>
</el-table-column>
</el-table>

<el-dialog title="命座信息" :visible.sync="dialogVisible" width="30%">
    <el-form>
        <el-form-item label="ID">
            <el-input v-model="form.id" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="命座">
            <el-input v-model="form.name" placeholder=""></el-input>
        </el-form-item>
    </el-form>
    <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="onSubmit">确 定</el-button>
  </span>
</el-dialog>
</div>
</template>

<script>
    import request from "../utils/request"
    export default {
        data() {
            return {
                fateList: [],
                dialogVisible: false,
                form: {
                    name: '',
                    id: null,
                }
            }
        },
        methods: {
            getFateList() {
                request.get("/fate").then((res) => {
                    this.fateList = res.data.data;
                })
            },
            rowClass() { //表头居中显示
                return "text-align:center"
            },
            onSubmit() {
                request
                    .post(`/fate`, this.form)
                    .then(() => {
                        this.getFateList();
                        this.dialogVisible = false;
                    });
            },
            del(id) {
                this.$confirm("此操作将永久删除该文件, 是否继续?", "提示", {
                        confirmButtonText: "确定",
                        cancelButtonText: "取消",
                        type: "warning"
                    })
                    .then(() => {
                        request
                            .delete(`/fate/${id}`)
                            .then(() => {
                                this.$message({
                                    type: "success",
                                    message: "删除成功!"
                                });
                                this.getFateList();
                            });
                    })
                    .catch(() => {
                        this.$message({
                            type: "info",
                            message: "已取消删除"
                        });
                    });
            },
        },
        created() {
            this.getFateList();
        },

    }
</script>